<template>
  <div>
    <h1>商品管理</h1>
    <el-button type="primary" @click="dialogVisible = true">添加商品</el-button>
    <el-dialog title="添加商品" :visible.sync="dialogVisible" width="30%">
      <el-form :model="newProduct" label-width="80px">
        <el-form-item label="商品名称">
          <el-input v-model="newProduct.name"></el-input>
        </el-form-item>
        <el-form-item label="商品价格">
          <el-input v-model.number="newProduct.price"></el-input>
        </el-form-item>
        <el-form-item label="商品描述">
          <el-input v-model="newProduct.description" type="textarea"></el-input>
        </el-form-item>
        <!-- 新增：图片上传 -->
        <el-form-item label="商品图片">
          <el-upload
            action="#"
            :auto-upload="false"
            :on-change="handleImageChange"
            :limit="1"
            accept="image/*"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
          <!-- 图片预览 -->
          <div v-if="newProduct.image" style="margin-top: 10px;">
            <img :src="newProduct.image" style="width: 100px; height: 100px; object-fit: cover;" />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addProduct">确 定</el-button>
      </span>
    </el-dialog>
    <el-table :data="localProducts" style="width: 100%">
      <el-table-column prop="name" label="商品名称"></el-table-column>
      <el-table-column prop="price" label="商品价格"></el-table-column>
      <el-table-column prop="description" label="商品描述"></el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button size="mini" @click="editProduct(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteProduct(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <h2>销售统计</h2>
    <el-table :data="salesData" style="width: 100%">
      <el-table-column prop="name" label="商品名称"></el-table-column>
      <el-table-column prop="quantity" label="销售数量"></el-table-column>
      <el-table-column prop="revenue" label="销售额"></el-table-column>
    </el-table>
    <el-divider></el-divider>
    <el-row :gutter="20">
      <el-col :span="6">
        <div>
          <el-statistic
            group-separator=","
            :precision="2"
            :value="totalRevenue"
            title="总销售额"
          ></el-statistic>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-statistic title="男女用户比例">
            <template slot="formatter">
              {{ maleRatio }}% / {{ femaleRatio }}%
            </template>
          </el-statistic>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-statistic
            group-separator=","
            :precision="2"
            :value="averageOrderValue"
            title="平均订单价值"
          ></el-statistic>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-statistic :value="feedbackCount" title="用户反馈">
            <template slot="suffix">
              <span @click="like = !like" class="like">
                <i
                  class="el-icon-star-on"
                  style="color:red"
                  v-show="!!like"
                ></i>
                <i class="el-icon-star-off" v-show="!like"></i>
              </span>
            </template>
          </el-statistic>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      like: true,
      totalRevenue: 0,
      maleRatio: 55,
      femaleRatio: 45,
      averageOrderValue: 0,
      feedbackCount: 120,
      localProducts: [],
      salesData: [
        { name: '华为 Mate 60 Pro', quantity: 10, revenue: 79990 },
        { name: '苹果 iPhone 15 Pro Max', quantity: 5, revenue: 54995 },
        { name: '三星 Galaxy S23 Ultra', quantity: 8, revenue: 79992 },
      ],
      dialogVisible: false,
      newProduct: {
        name: '',
        price: 0,
        description: '',
        image: '', // 新增：商品图片字段
      },
    };
  },
  methods: {
    // 新增：处理图片上传
    handleImageChange(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.newProduct.image = e.target.result; // 将图片转换为Base64格式存储
      };
      reader.readAsDataURL(file.raw);
    },
    addProduct() {
      if (!this.newProduct.name || !this.newProduct.price) {
        this.$message.error('商品名称和价格不能为空');
        return;
      }
      const newProduct = {
        ...this.newProduct,
        id: Date.now(), // 生成一个唯一的ID
      };
      this.localProducts.push(newProduct);
      this.$message.success('商品已添加');
      this.dialogVisible = false;
      this.$emit('update:products', this.localProducts); // 通知父组件更新
      // 重置表单
      this.newProduct = {
        name: '',
        price: 0,
        description: '',
        image: '', // 重置图片字段
      };
    },
    editProduct(product) {
      this.$prompt('请输入新的商品名称', '编辑商品', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: product.name,
      }).then(({ value }) => {
        const index = this.localProducts.findIndex(p => p.id === product.id);
        if (index !== -1) {
          this.localProducts[index].name = value;
          this.$message.success('商品名称已更新');
          this.$emit('update:products', this.localProducts); // 通知父组件更新
        }
      }).catch(() => {
        this.$message.info('取消编辑');
      });
    },
    deleteProduct(product) {
      this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.localProducts = this.localProducts.filter(p => p.id !== product.id);
        this.$message.success('删除成功');
        this.$emit('update:products', this.localProducts); // 通知父组件更新
      }).catch(() => {
        this.$message.info('已取消删除');
      });
    },
    updateSalesData() {
      this.totalRevenue = this.salesData.reduce((sum, item) => sum + item.revenue, 0);
      this.averageOrderValue = this.totalRevenue / this.salesData.length;
    },
  },
};
</script>

<style scoped>
/* 添加一些样式 */
.el-dialog {
  border-radius: 8px;
}
.el-button {
  margin-top: 10px;
}
.like {
  cursor: pointer;
  font-size: 25px;
  display: inline-block;
}
</style>